.project {
    width: 100%;
    display: flex;
    flex-direction: column;
    margin-bottom: 40px;
    cursor: pointer;
    z-index: 1;

}

.project:last-child {
    margin-bottom: 0;
}

.imgContainer {
    width: 100%;
    height: 300px;
    position: relative;
    background-color: rgba(0, 0, 0, 0.5)
}

.imgContainer h3 {
    position: absolute;
    bottom: 40px;
    left: 40px;
    font-size: 34px;
    font-weight: 300;
}

.imgContainer h3::after {
    content: '';
    background-color: #fff;

    position: relative;
    bottom: -10px;
    left: 0;

    display: block;
    width: 40px;
    height: 1px;
}

@media screen and (max-width: 1280px) {
    .imgContainer {
        max-width: 500px;
        height: 250px;
    }
}

@media screen and (max-width: 1050px) {
    .imgContainer {
        max-width: 400px;
        height: 200px;
    }

    .imgContainer h3 {
        font-size: 25px;
    }
}
